import React from 'react';
import { Link } from 'react-router-dom';




const Modal = ({ 
  error_code, 
  error_data, 
  duringTheTest, 
  handleCloseModal, 
  handleNewSubs,
}) => {


  React.useEffect(() => {
    document.body.style.overflow = 'hidden';
    return () => {
      document.body.style.overflow = 'unset';
    }
  }, [])


  return (
    <React.Fragment>
      <div className='modal'>
        <div className='modal__inner' >

          { error_code === 1 &&  
            <UnfinishedExamModal 
              error_data = { error_data } 
              handleCloseModal = { handleCloseModal }
            />}
          
          { error_code === 3 &&
            <NoMoneyModal
              handleCloseModal = { handleCloseModal } 
            /> }

          { error_code === 5 &&
            <NoQuestionModal 
              handleCloseModal = { handleCloseModal }
            /> }
          
          { error_code === 6 && 
            <SimpleModal
              error_message = ',   '
              handleCloseModal = { handleCloseModal } 
            /> }
          
          { error_code === 2 && 
            <SimpleModal 
              error_message = '      '
              handleCloseModal = { handleCloseModal }
            />}
          
          { error_code === 4 &&
            <SimpleModal
              error_message = '  .'
              handleCloseModal = { handleCloseModal }
            />}
          
          { error_code === 7 &&
            <UnfinishedSubsModal
              handleNewSubs = { handleNewSubs }
              handleCloseModal = { handleCloseModal } 
            /> }
          

        </div>
      </div>

      { duringTheTest && <div className='modal__empty'></div>}
    </React.Fragment>

  )
}

const UnfinishedExamModal = ({ error_data, handleCloseModal }) => {
  return (
    <React.Fragment>
      <div className='modal__header'>
        <div className='modal__title'>   </div>
      </div>

      <div className = 'modal__content'>
        <div className = 'modal__description'> 
               <b>{error_data.name_ru}</b>. 
               ,   . 
        </div>
      </div>

      <div className='modal__footer'>
        <button onClick={handleCloseModal} className='button button__over'>  </button>
        <Link
          onClick={handleCloseModal}
          className='button button__continue'
          to={`/start_test/${error_data.uuid}`}>
          
        </Link>
      </div>

    </React.Fragment>
  )
}



const NoMoneyModal = ({ handleCloseModal }) => {
  return (
    <React.Fragment>
      <div className='modal__header'>
        <div className='modal__title'>         </div>
      </div>

      <div className = 'modal__content'>
        <div className = 'modal__description'> 
                    
               <Link 
            to = '/profile/wallet' 
            className = 'modal__link'
            onClick={handleCloseModal}> 
             
          </Link>
        </div>
      </div>

      <div className='modal__footer'>
        <button onClick={handleCloseModal} className='button button__over'>  </button>
        <Link
          onClick={handleCloseModal}
          className='button button__continue'
          to={`/subscription`}>
           
        </Link>
      </div>

    </React.Fragment>
  )
}



const NoQuestionModal = ({ handleCloseModal }) => {
  return (
    <React.Fragment>
      <div className='modal__header'>
        <div className='modal__title'> ,  . </div>
      </div>

      <div className = 'modal__content'>
        <div className = 'modal__description'>
          ,  .         .   :)
        </div>
      </div>

      <div className='modal__footer'>
        <button onClick={handleCloseModal} className='button button__over'>  </button>
      </div>

    </React.Fragment>
  )
}



const SimpleModal = ({ handleCloseModal, error_message }) => {
  return (
    <React.Fragment>
      <div className='modal__header'>
        <div className='modal__title'> ,  . </div>
      </div>

      <div className = 'modal__content'>
        <div className = 'modal__description'>
          { error_message }
        </div>
      </div>

      <div className='modal__footer'>
        <button onClick={handleCloseModal} className='button button__over'>  </button>
      </div>

    </React.Fragment>
  )
}

const UnfinishedSubsModal = ({ handleCloseModal, handleNewSubs }) => {
  return (
    <React.Fragment>
      <div className='modal__header'>
        <div className='modal__title'> ,  . </div>
      </div>

      <div className = 'modal__content'>
        <div className = 'modal__description'>
              .       ?
        </div>
      </div>

      <div className='modal__footer'>
        <button onClick={handleCloseModal} className='button button__over'>  </button>
        <button type='submit' value='SUBMIT' className='button button__continue' onClick = { handleNewSubs }>  </button>
      </div>

    </React.Fragment>
  )
}





export default Modal;